.banner {
    position: relative;
    height: 880px;
    &__content {
        position: relative;
    }
    &__logo {
        position: absolute;
        width: 123px;
        top: -75px;
        right: -90px;
    }
    &__text1 {
        font-family: TeXGyreSchola-Regular;
        font-size: sz(20.54);
        line-height: sz(28);
        text-align: center;
        letter-spacing: sz(6.49);
        color: #fff;
    }
    &__text2 {
        margin-bottom: sz(14);
        font-family: TeXGyreSchola-Regular;
        font-size: sz(85);
        line-height: sz(112);
        text-align: center;
        letter-spacing: sz(4.72);
        color: #fff;
    }
    &__text3 {
        font-family: TeXGyreSchola-Regular;
        font-size: sz(20.54);
        line-height: sz(28);
        text-align: center;
        letter-spacing: sz(4);
        color: #fff;
    }
}

@media (max-width: 991px) {
    .banner {
        height: 680px;
        &__logo {
            top: -75px;
            right: -50px;
        }
    }
}

@media (max-width: 767px) {
    .banner {
        &__logo {
            width: 100px;
            top: 210px;
            right: 50%;
            margin-right: -50px
        }
    }
}

@media (max-width: 575px) {
    .banner {
        height: 580px;
        &__logo {
            top: 180px;
        }
        &__text1 {
            font-size: sz(16);
        }
        &__text2 {
            font-size: sz(44);
            line-height: sz(60);
        }
        &__text3 {
            font-size: sz(16);
        }
    }
}


